.sk-slider .slider-holder {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}
.sk-slider .blaze-slider {
  --slides-to-show: 1;
  /* for loop: false */
}
@media (max-width: 1024px) {
  .sk-slider .blaze-slider {
    --slides-to-show: 1;
  }
}
@media (max-width: 720px) {
  .sk-slider .blaze-slider {
    --slides-to-show: 1;
  }
}
.sk-slider .blaze-slider.dragging .blaze-track {
  cursor: grabbing;
}
.sk-slider .blaze-slider.start .blaze-prev, .sk-slider .blaze-slider.end .blaze-next {
  opacity: 0.5;
  cursor: not-allowed;
}
.sk-slider .blaze-slider .blaze-pagination {
  display: flex;
  gap: 12px;
}
.sk-slider .blaze-slider .blaze-pagination button {
  cursor: pointer;
  font-size: 0;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  outline: none;
  border: none;
  background: #939598;
  transition: transform 200ms ease, background-color 300ms ease;
}
.sk-slider .blaze-slider .blaze-pagination button.active {
  background: #002ea6;
}
.sk-slider .blaze-slider .controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  position: absolute;
  left: 0;
}
.sk-slider .blaze-slider .pagination-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  gap: 20px;
  width: 100%;
}
.sk-slider .slide {
  list-style-type: none;
}
.sk-slider .blaze-prev,
.sk-slider .blaze-next {
  z-index: 10;
  border: none;
  width: 25px;
  height: 25px;
  padding: auto;
  cursor: pointer;
  background-position: center center;
  background-size: 100%;
  background-repeat: no-repeat;
  transition: background-image 0.3s ease-in-out;
  background-image: url("/wp-content/themes/custom_theme/assets/media/icons/slider.svg");
}
.sk-slider .blaze-prev[disabled],
.sk-slider .blaze-next[disabled] {
  display: none !important;
  opacity: 0.5;
  cursor: not-allowed;
}
.sk-slider .blaze-prev:hover, .sk-slider .blaze-prev:focus,
.sk-slider .blaze-next:hover,
.sk-slider .blaze-next:focus {
  background-image: url("/wp-content/themes/custom_theme/assets/media/icons/slider-blue.svg");
}
.sk-slider .blaze-next {
  transform: rotate(180deg);
}